.comment-index {
    display: grid;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    background: $bg-clr1;
    margin: auto;
    margin-top: 2rem;
    padding: 2.5rem;
    width: 60%;

    .comments-wrapper {
        padding: 1rem;
        background-color: lighten($bg-clr5, 8%);
        & > * {
            margin-bottom: 1.5rem;
        }
    }
}
