.app-header {
    background: $text-clr1;
    color: $text-clr2;
    .layout-wrapper {
        display: flex;
        .logo {
            display: flex;
            width: 40px;
        }
        .links {
            display: flex;
            margin-left: auto;
            align-items: center;
            li {
                height: 100%;
                a {
                    display: inline-flex;
                    align-items: center;
                    height: 100%;
                    padding: 1rem;
                    color: $text-clr2;
                    box-shadow: -1px 0 0 0 black;
                    border-bottom: $bg-clr2 3px solid;
                    &.active {
                        color: $bg-clr4;
                        border-bottom: $bg-clr4 3px solid;
                    }
                }
            }
            & li:last-child {
                box-shadow: 1px 0 0 0 black;
            }
        }
    }
}
