* {
    box-sizing: border-box;
}

html {
    font-family: 'Livvic', sans-serif;
    color: $text-clr1;
    line-height: 1.8;

    // @media (max-width: 1300px) {
    //     font-size: 13px;
    // }
}

body {
    margin: 0;
    background-color: $bg-clr5;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

img {
    width: 100%;
}

p {
    font-size: rem(18px);
    margin: 0;
    font-weight: 600;
}

button,
.btn {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    padding: rem(8.5px) rem(32.75px) rem(10.5px);
    color: $text-clr1;
    border: none;
    font-family: 'Livvic', sans-serif;
    text-align: center;
    background-color: $bg-clr4;
    border-radius: 4px;
    color: $text-clr2;
    height: 100%;
    width: -webkit-max-content;
}

// links
a {
    color: $text-clr1;
    text-decoration: none;
}
