.comment-edit {
    display: grid;
    background: $bg-clr6;
    padding: 1rem;

    form {
        display: grid;
        gap: 0.5rem;
        padding-bottom: 1rem;

        button {
            justify-self: end;
        }

        input {
            outline: none;
            padding: 0.35rem;
        }

        textarea {
            padding: 0.2rem 0 0 0.35rem;
            font-size: 1rem;
        }
    }
}
